
import configuration from "./component/configuration.vue";
import { defineComponent, ref } from "vue";
import { Toast } from "vant";
import { useRouter } from 'vue-router'

export default defineComponent({
  name: "Camera",
  components: {
    configuration,
  },
  setup() {
    const value = ref(50);
    let isPlay = ref(true);
    const progressValue = ref(55);
    const $router = useRouter()
    const tip = () => {
      Toast("提示内容");
    };
    const back = () => {
      $router.go(-1);
    };
    const onDistanceChange = (value: number) => {
      console.log(value);
    };
    const onprogressChange = (value: number) => {
      console.log(value);
    };
    const OnchangePlay = () => {
      isPlay.value = !isPlay.value;
      let videoId: any = null;
      videoId = document.getElementById("ghostblade");
      console.log(videoId);
      if (videoId?.paused) {
        setTimeout(() => {
          videoId.play();
        }, 300);
      } else {
        videoId.pause();
      }
    };
    return {
      value,
      progressValue,
      isPlay,
      tip,
      back,
      onDistanceChange,
      onprogressChange,
      OnchangePlay,
    };
  },
})
