
import { onMounted, ref } from "vue";
import { Toast } from "vant";
const icon3 = require("@/assets/image/photo/icon3.png");
export default {
  setup() {
    let currentBtnIndex = ref(0);
    let currentImgIndex = ref(0);
    const tabs = ref(["参数", "滤镜"]);
    const list = ref(["环绕镜头", "推拉镜头", "自由镜头"]);
    const groupChecked = ref([]);
    const wideAngleValue = ref(50);
    const emptinessValue = ref(50);
    const filterList = ref<number[]>([]);
    const loading = ref(false);
    const finished = ref(false);
    const refreshing = ref(false);
    const go = () => {
      console.log(groupChecked.value);
    };
    const onTab = (index: number) => {
      currentBtnIndex.value = index;
    };
    const onImgChange = (index: number) => {
      currentImgIndex.value = index;
    };
    const onwideAngleChange = (value: number) => {
      console.log(value);
    };
    const onemptinessChange = (value: number) => {
      console.log(value);
    };
    const tip = () => {
      Toast("提示内容");
    };

    const onLoad = () => {
      setTimeout(() => {
        if (refreshing.value) {
          filterList.value = [];
          refreshing.value = false;
        }

        for (let i = 0; i < 10; i++) {
          filterList.value.push(filterList.value.length + 1);
        }
        loading.value = false;

        if (filterList.value.length >= 20) {
          finished.value = true;
        }
      }, 1000);
    };

    const onRefresh = () => {
      // 清空列表数据
      finished.value = false;

      // 重新加载数据
      // 将 loading 设置为 true，表示处于加载状态
      loading.value = true;
      onLoad();
    };

    onMounted(() => {
      onLoad();
      console.log("Component is mounted!");
    });
    return {
      icon3,
      currentBtnIndex,
      currentImgIndex,
      list,
      filterList,
      refreshing,
      finished,
      tabs,
      loading,
      groupChecked,
      wideAngleValue,
      emptinessValue,
      go,
      tip,
      onTab,
      onwideAngleChange,
      onemptinessChange,
      onLoad,
      onRefresh,
      onImgChange,
    };
  },
};
